<template>
  <Sheet :value="value" width="540px" has-footer @input="closeSheet">
    <!-- title -->

    <template #title>{{ field ? "Edit" : "Add" }} Field</template>

    <!-- ... -->

    <!-- field details -->

    <template #default>
      <div class="q-pa-md">
        <ValidationObserver ref="form">
          <!-- name -->

          <ValidationProvider
            v-slot="{ errors }"
            name="name"
            :rules="{ required: true }"
          >
            <TextField
              v-model="name"
              label="name"
              is-mandatory
              :error="errors[0]"
              class="q-mb-lg"
            />
          </ValidationProvider>

          <!-- ... -->

          <!-- is mandatory -->

          <CheckboxField
            v-model="isMandatory"
            label="Mark this field as required"
            :is-disabled="disableIsMandatory"
            class="q-mb-md"
          />

          <!-- ... -->

          <!-- include in folder structure -->

          <CheckboxField
            v-model="includeInFolderStructure"
            label="Include this field in the folder structure"
            :is-disabled="disableIncludeInFolderStructure"
            class="q-mb-lg"
          />

          <!-- ... -->

          <!-- data type -->

          <SelectField
            v-model="dataType"
            label="data type"
            is-mandatory
            :is-clearable="false"
            :options="dataTypes"
            class="q-mb-lg"
          />

          <!-- ... -->

          <!-- data type options -->

          <NumberFieldOptions
            v-if="dataType === 'NUMBER'"
            :allow-decimals.sync="options.allowDecimals"
          />

          <BooleanFieldOptions
            v-if="dataType === 'BOOLEAN'"
            :truthy-value.sync="options.truthyValue"
            :falsy-value.sync="options.falsyValue"
          />

          <SelectFieldOptions
            v-if="dataType === 'SINGLE_SELECT' || dataType === 'MULTI_SELECT'"
            :options-type.sync="options.optionsType"
            :master-form-id.sync="options.masterFormId"
            :predefined-options.sync="options.predefinedOptions"
            :master-table-column.sync="options.masterTableColumn"
            :allow-to-add-new-options.sync="options.allowToAddNewOptions"
            :master-form-parent-column.sync="options.masterFormParentColumn"
            :mandatory-settings.sync="options.mandatorySettings"
            :fields="_fields"
            :non-mandatory-fields="nonMandatoryFields"
          />

          <TableFieldOptions
            v-if="dataType === 'TABLE'"
            :columns.sync="options.columns"
          />

          <BarcodeFieldOptions
            v-if="dataType === 'BARCODE'"
            :barcode-type.sync="options.barcodeType"
            :barcode-prefix.sync="options.barcodePrefix"
            :barcode-length.sync="options.barcodeLength"
          />

          <!-- ... -->
          <AutoGeneratedOptions
            v-if="dataType === 'AUTO_GENERATED'"
            :field-columns="fieldsColumns"
            :auto-generated-options.sync="options.autoGeneratedOptions"
          />
        </ValidationObserver>
      </div>
    </template>

    <!-- ... -->

    <!-- footer -->

    <template #footer>
      <BaseButton
        is-flat
        label="cancel"
        class="q-mr-sm"
        @click="closeSheet(false)"
      />

      <BaseButton
        :label="field ? 'save' : 'add'"
        @click="field ? saveField() : addField()"
      />
    </template>

    <!-- ... -->
  </Sheet>
</template>

<script>
import { ValidationObserver, ValidationProvider } from "vee-validate";

import Sheet from "@/components/common/popup/Sheet.vue";
import TextField from "@/components/common/form/text-field/TextField.vue";
import CheckboxField from "@/components/common/form/checkbox-field/CheckboxField.vue";
import SelectField from "@/components/common/form/select-field/SelectField.vue";

import NumberFieldOptions from "./components/NumberFieldOptions.vue";
import BooleanFieldOptions from "./components/BooleanFieldOptions.vue";
import SelectFieldOptions from "./components/SelectFieldOptions.vue";
import TableFieldOptions from "./components/table-field-options/TableFieldOptions.vue";
import BarcodeFieldOptions from "./components/BarcodeFieldOptions.vue";
import AutoGeneratedOptions from "./components/AutoGeneratedOptions.vue";

import DataType from "@/constants/data-type.js";
import { lowerCase, startCase } from "lodash-es";

export default {
  name: "FieldDetails",

  components: {
    Sheet,
    ValidationObserver,
    ValidationProvider,
    TextField,
    CheckboxField,
    SelectField,
    NumberFieldOptions,
    BooleanFieldOptions,
    SelectFieldOptions,
    TableFieldOptions,
    BarcodeFieldOptions,
    AutoGeneratedOptions,
  },

  props: {
    value: {
      type: Boolean,
      default: false,
    },

    field: {
      type: Object,
      default: null,
    },

    fields: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      name: "",
      isMandatory: false,
      includeInFolderStructure: false,
      dataType: "",
      options: {
        allowDecimals: true,
        truthyValue: "true",
        falsyValue: "false",
        optionsType: "EXISTING",
        masterFormId: 0,
        masterTableColumn: "",
        predefinedOptions: [],
        allowToAddNewOptions: true,
        columns: [],
        barcodeType: "",
        barcodePrefix: "",
        barcodeLength: "",
        autoGeneratedOptions: [
          {
            id: this.$nano.id(),
            key: "seperator",
            value: "-",
          },
          {
            id: this.$nano.id(),
            key: "autoIncrement",
            value: 1,
          },
        ],
        masterFormParentColumn: "",
        mandatorySettings: [],
      },
      fieldsColumns: [],
      autoGeneratedOptions: [
        {
          id: this.$nano.id(),
          key: "seperator",
          value: "-",
        },
        {
          id: this.$nano.id(),
          key: "autoIncrement",
          value: 1,
        },
      ],
    };
  },

  computed: {
    dataTypes() {
      const dataTypes = Object.keys(DataType);
      return dataTypes.map((dataType) => ({
        id: this.$nano.id(),
        label: dataType === "OMR" ? dataType : startCase(lowerCase(dataType)),
        value: dataType,
      }));
    },

    disableIsMandatory() {
      return this.includeInFolderStructure || this.dataType === "TABLE";
    },

    disableIncludeInFolderStructure() {
      return this.dataType === "TABLE";
    },

    _fields() {
      let field = [];
      this.fields.forEach((item) => {
        if (item.name !== this.field?.name) {
          let options = JSON.parse(item.options);
          if (options.masterFormId === this.options.masterFormId) {
            field.push({
              id: this.$nano.id(),
              label: item.name,
              value: item.name,
            });
          }
        }
      });
      return field;
    },

    nonMandatoryFields() {
      let fields = [];
      this.fields.forEach((field) => {
        if (field.name !== this.field?.name) {
          if (!field.isMandatory) {
            fields.push({
              id: this.$nano.id(),
              label: field.name,
              value: field.name,
            });
          }
        }
      });
      return fields;
    },
  },

  watch: {
    field: {
      immediate: true,
      handler() {
        if (this.field) {
          this.name = this.field.name;
          this.isMandatory = this.field.isMandatory;
          this.includeInFolderStructure = this.field.includeInFolderStructure;
          this.dataType = this.field.dataType;
          this.options = JSON.parse(this.field.options);
          if (!this.options.autoGeneratedOptions.length) {
            this.options.autoGeneratedOptions = [
              {
                id: this.$nano.id(),
                key: "seperator",
                value: "-",
              },
              {
                id: this.$nano.id(),
                key: "autoIncrement",
                value: 1,
              },
            ];
          }
        } else {
          this.reset();
        }
      },
    },

    includeInFolderStructure() {
      if (this.includeInFolderStructure) {
        this.isMandatory = true;
      }
    },

    dataType() {
      if (this.dataType === "TABLE") {
        this.isMandatory = false;
        this.includeInFolderStructure = false;
      }
    },

    fields: {
      deep: true,
      immediate: true,
      handler() {
        if (this.fields.length) {
          this.fieldsColumns = [];
          this.fields.forEach((item) => {
            if (item.dataType !== "AUTO_GENERATED") {
              this.fieldsColumns.push({
                id: this.$nano.id(),
                label: item.name,
                value: item.name,
              });
            }
          });
        }
      },
    },

    "options.optionsType": {
      immediate: true,
      handler() {
        if (this.options.optionsType === "EXISTING") {
          this.options.allowToAddNewOptions = true;
        } else {
          console.log(this.options.allowToAddNewOptions);
        }
      },
    },
  },

  methods: {
    reset() {
      this.name = "";
      this.isMandatory = false;
      this.includeInFolderStructure = false;
      this.dataType = "";
      this.options = {
        allowDecimals: true,
        truthyValue: "true",
        falsyValue: "false",
        optionsType: "EXISTING",
        masterFormId: 0,
        masterTableColumn: "",
        predefinedOptions: [],
        allowToAddNewOptions: true,
        columns: [],
        barcodeType: "",
        barcodePrefix: "",
        barcodeLength: "",
        autoGeneratedOptions: [
          {
            id: this.$nano.id(),
            key: "seperator",
            value: "-",
          },
          {
            id: this.$nano.id(),
            key: "autoIncrement",
            value: 1,
          },
        ],
        masterFormParentColumn: "",
      };
    },

    closeSheet() {
      this.$emit("input", false);
      this.reset();
    },

    async addField() {
      const isValid = await this.$refs.form.validate();
      if (this.dataType === "AUTO_GENERATED") {
        // const isValidAuto = await this.$refs.autoGenerated.validate();
        // if (!isValidAuto) {
        //   return;
        // }
      }

      if (!isValid) {
        return;
      }

      this.$emit("add", {
        _id: this.$nano.id(),
        name: this.name,
        dataType: this.dataType,
        isMandatory: this.isMandatory,
        includeInFolderStructure: this.includeInFolderStructure,
        level: 0,
        options: JSON.stringify(this.options),
      });

      this.closeSheet();
    },

    async saveField() {
      const isValid = await this.$refs.form.validate();

      if (!isValid) {
        return;
      }
      this.$emit("save", {
        _id: this.field._id,
        id: this.field.id,
        name: this.name,
        dataType: this.dataType,
        isMandatory: this.isMandatory,
        includeInFolderStructure: this.includeInFolderStructure,
        level: 0,
        options: JSON.stringify(this.options),
      });

      this.closeSheet();
    },
  },
};
</script>

<style lang="scss" scoped></style>
