<template>
  <FormSection
    :icon="isActive ? 'mdi-briefcase' : 'mdi-briefcase-outline'"
    :is-active="isActive"
    v-on="$listeners"
  >
    <template #title>Workspace Details</template>
    <template #description>
      Select the digital workspace of your choice to create a new root folder
      and associate it with.
    </template>

    <!-- workspace -->

    <ValidationProvider
      v-slot="{ errors }"
      name="workspace"
      :rules="{ required: true }"
    >
      <SelectField
        :value="workspaceId"
        label="workspace"
        is-mandatory
        :options="workspaces"
        :is-readonly="mode === 'EDIT'"
        :error="errors[0]"
        class="q-mb-lg"
        @input="updateWorkspace"
      />
    </ValidationProvider>

    <!-- ... -->
  </FormSection>
</template>

<script>
import { ValidationProvider } from "vee-validate";

import FormSection from "@/components/common/item-builder/FormSection.vue";
import SelectField from "@/components/common/form/select-field/SelectField.vue";

import { workspace } from "@/api/factory.js";

export default {
  name: "WorkspaceDetails",

  components: {
    FormSection,
    ValidationProvider,
    SelectField,
  },

  props: {
    workspaceId: {
      type: [String, Number],
      required: true,
    },

    isActive: {
      type: Boolean,
      default: false,
    },

    mode: {
      type: String,
      default: "NEW",
    },
  },

  data() {
    return {
      workspaces: [],
    };
  },

  created() {
    this.getWorkspaces();
  },

  methods: {
    updateWorkspace(workspaceId) {
      this.$emit("update:workspaceId", workspaceId);
    },

    /* api functions */

    async getWorkspaces() {
      const { error, payload } = await workspace.getWorkspaceList();

      if (error) {
        this.$alert.error("Error fetching workspaces");
        return;
      }

      this.workspaces =
        payload &&
        payload.map((workspace) => ({
          id: this.$nano.id(),
          label: workspace.value,
          value: workspace.id,
        }));
    },

    /* ... */
  },
};
</script>

<style lang="scss" scoped></style>
